<template>
<van-nav-bar
    title="积分记录"
    left-arrow
    @click-left="gotoleft"
  />
<div class="pointsRecord-container">
  <van-image class="bg" :src="bg"></van-image>

  <div class="points">
    <text>积分：</text>
    <text>{{total}}</text>
  </div>
  <div class="record-list">
    <div class="record-item" v-for="item in recordList" :key="item.time">
      <div class="top">
        <text class="name">{{item.name}}</text>
        <text class="point">{{item.points}}</text>
      </div>
      <div class="date">{{item.createTime}}</div>
    </div>
  </div>
</div>
</template>

<script>
import router from '../router'
import bg from '../assets/images/bg.png'
import {IntegralRecord} from '@/api'
import {reactive,toRefs} from 'vue'
export default {
  name: 'pointsRecord',
  setup(props) {
    //参数
    const code = 0
    let mine = reactive({
      recordList:[],
      total:0,
      bg:bg
    })
    IntegralRecord().then(res=>{
      console.log('get points record==>',res)
      
      if(res.code===0){
        let recordList =  []
        let obj = {}
        res.data.forEach(item=>{
          obj = {...item}
          obj.name = item.describe.split('：')[0]
          obj.points = item.describe.split('：')[1]
          recordList.push(obj)
        })
        // this.setData({recordList,total:res.data.sum})
        mine.recordList=recordList
        mine.total=res.sum
      }
    })
    function gotoleft(){
      router.go(-1)
    }
    //挂载
    return{ ...toRefs(mine),gotoleft}
  }
}
</script>

<style scoped>
page{
  width:100%;
  height: 100%;
}
.pointsRecord-container{
  position: relative;
  display: flex;
  flex-direction: column;
  align-items: center;
  width:100%;
  height: 100%;
  padding-top: 50px;
  box-sizing: border-box;
  overflow: hidden;
}

.bg{
  position: absolute;
  top:0;
  left:0;
  display: block;
  width:100%;
  z-index: -1;
}

.points{
  text-align: center;
  color:#ffffff;
}

.points text:nth-child(2){
  font-size: 70px;
}

.record-list{
  flex:1;
  width:80%;
  margin-top: 20px;
  padding:30px;
  border-radius: 30px;
  background-color: #ffffff;
  overflow-y: scroll;
}

.record-list .record-item{
  padding-bottom: 20px;
  border-bottom:1px solid var(--divide-color);
}

.record-list .record-item:not(:first-of-type){
  margin-top: 20px;
}

.record-list .record-item:last-of-type{
  border:none;
}

.record-item .top{
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.record-item .point{
  color:var(--red-color);
}

.record-item .date{
  margin-top: 5px;
  font-size: 28px;
  color:var(--text-color);
}
</style>